body {
  background: linear-gradient(0deg, #225c9b 0, #225c87);
  color: #0f0f0f;
  font-family: "Poppins", sans-serif;
  font-size: 1rem;
  line-height: 1.5em;
  font-weight: 600;
  margin: 30px;
  height: calc(100vh - 60px);
  display: flex;
  align-items: center;
  justify-content: center;
}
.container {
  background-color: #ffffff;
  width: 25vw;
  padding: 30px;
  border-radius: 30px;
  box-shadow: 2px 2px 5px #9fa8da;
}
.title{
  color: #38b2ac;
  text-align: center;
  display: block;
  font-size: 30px;
  font-weight: 1000;
  margin-bottom: 10px;
}
.group{
  background: #F9F9F9;
  padding: 25px;
  margin: 150px 0;
  box-shadow: 0 0 20px 0 rgba(0, 0, 0, 0.2), 0 5px 5px 0 rgba(0, 0, 0, 0.24);
}
input {
  border: 1px solid #8c8c8c;
  color: #0f0f0f;
  background-color: #ffffff;
  font-family: "Poppins", sans-serif;
  font-size: 1em;
  font-weight: 300;
  padding: 5px 30px 5px 5px;
  width: calc(100% - 35px);
}
button{
  padding: 10px 20px;
  font-size: 1.2em;
  background-color: #007bff;
  color: white;
  border-radius: 5px;
  border: none;
  box-shadow: 0px 0px 5px gray;
  cursor: pointer;
  margin-top: 20px;
}